import { Component, OnInit, DoCheck } from '@angular/core';
import  $  from "jquery";

@Component({
  selector: 'panel',
  templateUrl: './panel.component.html',
  styleUrls: ['./panel.component.css']
})
export class PanelComponent implements OnInit,DoCheck {

  public slogan : string;

  constructor() {
    console.log("Hola estoy jugando");
    
   }

  ngOnInit() {
    $('#mobile-menu-toggler').on('click', function () {
      if ($('.mobile-menu').find('ul').first().is(':visible')) {
        $('.mobile-menu').find('ul').first().slideUp();
      } else {
        $('.mobile-menu').find('ul').first().slideDown();
      }
    });
  }

  ngDoCheck(){
    console.log("Docheck ejecutado");
    
  }
  
  CambiarSlogan(){
    this.slogan = "Total de tareas pendiente";
  }

}
