import { ModuleWithProviders } from "@angular/core"; // Modulo para crear las rutas
import { Routes, RouterModule } from "@angular/router";

// Componentes 
import { LoginComponent } from './component/login/login.component';
import { SignupComponent } from './component/signup/signup.component';
import { PanelComponent } from './component/panel/panel.component';
import { NewtaskComponent } from './component/newtask/newtask.component';
import { NewprojectComponent } from './component/newproject/newproject.component';
import { EventComponent } from './component/event/event.component';
import { NewtaskdoComponent } from './component/newtaskdo/newtaskdo.component';

const approutes: Routes = [
    {path:'', component:LoginComponent},
    {path:'login', component:LoginComponent},
    {path:'signup', component:SignupComponent},
    {path:'panel', component:PanelComponent},
    {path:'nuevatarea', component:NewtaskComponent},
    {path:'nuevatareado', component:NewtaskdoComponent},    
    {path:'nuevoproyecto', component:NewprojectComponent},
    {path:'crearevento', component:EventComponent}
];

export const appRoutingProviders: any [] = [];
export const routing:ModuleWithProviders = RouterModule.forRoot(approutes);